import {dealLog, getCountryNameByDomain} from "@/utils/log-util";
import {getuuid} from "@/utils/uuid-util";
import {EXTENSION_ID} from "@/utils/extension-util";
import {loadZbaseConfigToStorage} from "zbase-popup-component/src/zbase/popup/utils/ZbasePopupContentUtil";
import {setInstallTime, updateSetInstallTime} from "zbase-popup-component/src/zbase/popup/utils/installTime";
import {runtime,tabs} from "webextension-polyfill";

let extension_id = EXTENSION_ID
let zbaseConfigUrl = 'https://waplus.io/user/api-chrome-extension/get-remote-config'
//
// let zbaseConfigUrl = 'https://dev.zfscrmteam.top/user/api-chrome-extension/get-remote-config'
chrome.runtime.onInstalled.addListener(function (details) {
    // set storage variables
    chrome.storage.sync.get([
        'on',
        'messages',
        'messagesPreview',
        'mediaPreview',
        'mediaGallery',
        'textInput',
        'profilePic',
        'name',
        'noDelay',
        'unblurActive'
    ], function (data) {
        data.on == null && chrome.storage.sync.set({on: true});
        data.messages == null && chrome.storage.sync.set({messages: true});
        data.messagesPreview == null && chrome.storage.sync.set({messagesPreview: true});
        data.textInput == null && chrome.storage.sync.set({textInput: true});
        data.mediaPreview == null && chrome.storage.sync.set({mediaPreview: true});
        data.mediaGallery == null && chrome.storage.sync.set({mediaGallery: true});
        data.profilePic == null && chrome.storage.sync.set({profilePic: false});
        data.name == null && chrome.storage.sync.set({name: false});
        data.noDelay == null && chrome.storage.sync.set({noDelay: false});
        data.unblurActive == null && chrome.storage.sync.set({unblurActive: false});
    });
    chrome.storage.local.get(['version','uuid'], async function (res) {
        let uuid = res.uuid !== undefined ? res.uuid : getuuid();
        let version = res.version !== undefined ? res.version : chrome.runtime.getManifest().version;
        if (details.reason === 'install') {
            loadZbaseConfigToStorage(extension_id, zbaseConfigUrl)
            let installTime = Math.round(new Date() / 1000)
            chrome.storage.local.set({installTime, uuid, version});
            dealLog({type: 'log', 'eventType': 100001, 'otherParams': {uuid, version}});
            const tabUrl2 = 'https://wawebsender.com/whatsapp-tools/?utm_source=privacy_download';
            const tabUrl = 'https://web.whatsapp.com/';
            chrome.tabs.create({url: tabUrl2}).then(() => {
                chrome.tabs.create({url: tabUrl})
            })
        }
    })

    // zbase
    let installTime = Math.round(new Date() / 1000)
    if (details.reason === 'install') {
        setInstallTime(installTime)
        chrome.storage.sync.set({initialDialog: {show: true, opened: false}})
        chrome.storage.sync.set({reviewInvitationDialog: {opened: false, installTime: new Date().getTime() / 1000}})
        chrome.storage.local.set({installTime})
    } else if (details.reason === 'update') {
        updateSetInstallTime(installTime)
        loadZbaseConfigToStorage(extension_id, zbaseConfigUrl)
    }
    chrome.storage.sync.set({navRedirectButton: {show: true}})
    chrome.storage.sync.set({initialDialog: {show: true, opened: false}})
    chrome.storage.sync.set({reviewInvitationDialog: {opened: false, installTime: new Date().getTime()}})
});

// //set icon depending on extension status
function toggleIcon(status) {
    chrome.action.setIcon({path: 'images/status' + (status == true ? 'On' : 'Off') + '.png'});
}

chrome.storage.onChanged.addListener(async function (changes, area) {
    if (area === 'sync') {
        if ('on' in changes) {
            toggleIcon(changes.on.newValue);
        }

        if ('lockPassword' in changes) {
            const waTabs = await tabs.query({active:true, currentWindow:true});
            await tabs.sendMessage(waTabs[0].id, {lockPassword: changes.lockPassword.newValue});
        }

    }

});

chrome.runtime.onMessage.addListener(function (data) {
    // zbase
    if (data.zbaseType === 'zbase-content-init') {
        loadZbaseConfigToStorage(extension_id, zbaseConfigUrl)
    }

    if (data.userPhoneNum) {
        let PhoneNumber = require('awesome-phonenumber');
        let c = PhoneNumber('+' + data.userPhoneNum).getRegionCode(); // -> 'SE'
        let pn = new PhoneNumber(data.userPhoneNum.toString(), c);
        let region = pn.getRegionCode() ? pn.getRegionCode() : '';
        let countryCode = getCountryNameByDomain(region) ? getCountryNameByDomain(region) : '';
        let phoneNum = data.userPhoneNum;
        chrome.storage.local.get(['installTime','uuid','version'], function (res) {
            let installTime = res.installTime !== undefined ? res.installTime : '';
            let uuid = res.uuid !== undefined ? res.uuid : '';
            let version = res.version !== undefined ? res.version : '';
            let uninstall_url =
                'https://wawebsender.com/waplus-privacy-uninstall/?utm_source=' +
                phoneNum +
                '&install_time=' +
                installTime +
                '&country_name=' +
                countryCode +
                '&uuid=' +
                uuid +
                '&version=' +
                version
                chrome.runtime.setUninstallURL(uninstall_url);
        })
    }
    //
    if (data.type === 'log') {
        dealLog(data);
    }
    // more-tools 
    if(data.type === 'url'){
        chrome.tabs.create({
            url:data.data,
            active:true
        })
    }
})

